import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import './StartPage.css'
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { NavLink } from 'react-router-dom';


const useStyles = makeStyles({
  root: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
});


export default function StartPage() {
  const classes = useStyles();

  return (
      <div className="startPage">
        <Card className={classes.root}>
            <CardContent>
                <Typography variant="h5" component="h2">
                (описание игры + настройки)
                </Typography>
                <NavLink to='game'><Button>Играть</Button></NavLink>
                <Typography variant="body2" component="p">
                  Приветствую!
                </Typography>
                <Typography variant="body2" component="p">
                  Данное задание я получил во вторник днем.
                </Typography>
                <Typography variant="body2" component="p">
                  Ответил, что отправлю готовый вариант до конца недели (хотя я думал, что справлюсь до пятницы), но как вы видите отправил позднее.
                </Typography>
                <Typography variant="body2" component="p">
                  Из-за последних событий, мое выступление на конференции (в университете) перенесли на субботу, поэтому мне пришлось отложить все свои дела и готовить доклад.
                </Typography>
                <Typography variant="body2" component="p">
                  На работу ушло +-8 часов
                </Typography>
                <Typography variant="body2" component="p">
                  На UI часть не хватило времени, поэтому выглядит она не ахти 
                </Typography>
                
            </CardContent>
        </Card>
    </div>
  );
}
